	module.exports = {"data":{"markdownRemark":{"html":"<p>It is the one on the left side of the image. ( <a href=\"/3066\">By the way the image is the one of October 27th, 2002</a> ) First complete PC for yourself. I bought it for less than 8,000 yen from Mr. TN of local acquaintance. I thought that there was evidence that Windows was stuck around Grabo stuck, but HDD did not recognize it (because it exceeded the capacity that can be recognized? Or crash?) Actually it is a FDD exclusive machine. It might have been possible to run Windows on this PC if it was a bit more financial at that time.</p>\n<p>After that, I got a <a href=\"/2922\">new PC</a> at the end of 1998, which I used for BASIC for a while for a while but entered completely with <a href=\"/3007\">PC-9821 Cx / S3</a> acquisition, then sold for 10 yen at HARD OFF.</p>\n<h2>FM multi tone generator PC-9801-86</h2>\n<p>Multi sound source equipped with PCM + FM + PSG etc. loaded with YAMAHA YM2608. Although it is an ordinary sound card pierced by the old PC-98, even at that time it even became a sound source of a fine DTM. Everyone in the past 98 games was made on the basis of this. It seems that it has declined since Windows came out.</p>\n<p>At the time, I did not think about original songs and arrangements, I was doing DTM exclusively for ear copying purpose.</p>\n<h2>Works / Other things I did</h2>\n<ul>\n<li>(Original SS) <a href=\"/tag/evil-magic\">EVIL MAGIC</a></li>\n<li>(Original SS) <a href=\"/tag/doron\">勇者 ド ロ ン 末 賞 記 記</a></li>\n</ul>\n<h2>Performance</h2>\n<ul>\n<li>CPU: intel 386 SX (20 MHz) -> Ti 486 SLC 2 (40 MHz)</li>\n<li>RAM: 12.6 MB</li>\n<li>GPU: Alliance Promotion-6410</li>\n<li>VRAM: 1 MB</li>\n<li>HDD: 1 GB (Why not recognize)</li>\n<li>DRIVE: constant velocity CD-ROM drive (SCSI), 3.5 inch 2 HD FDD × 3 (3 mapped only for one)</li>\n<li>NET: (NONE)</li>\n<li>SOUND: Onboard (equivalent to PC - 9801 - 86 / PCM + FM 6 sound + PSG 3 sound + 6 rhythms)</li>\n<li>OS: MS-DOS 6.2</li>\n</ul>","frontmatter":{"cover":null,"date":"1997-11-18T20:00:00+09:00","strDate":"1997/11/18","title":"(1997-2003) NEC 98 MULTi model S 1 (PC-9821 first generation)"}}},"pathContext":{"path":"/en/blog/1997/11/18-pc-9821/","langKey":"en"}}

